import "./App.css";
import confetti from "canvas-confetti";
import { TypingEffect } from "react-typing-text-effect";
import { useEffect } from "react";
const Modal = (props) => {
  var duration = 45 * 1000;
  var animationEnd = Date.now() + duration;
  var defaults = { startVelocity: 10, spread: 360, ticks: 60, zIndex: 100 };
  var skew = 1;

  function randomInSnow(min, max) {
    return Math.random() * (max - min) + min;
  }

  (function frame() {
    var timeLeft = animationEnd - Date.now();
    var ticks = Math.max(200, 500 * (timeLeft / duration));
    skew = Math.max(0.8, skew - 0.001);

    confetti({
      particleCount: 1,
      startVelocity: 0,
      ticks: ticks,
      origin: {
        x: Math.random(),
        // since particles fall down, skew start toward the top
        y: Math.random() * skew - 0.2,
      },
      colors: ["#ffffff"],
      shapes: ["circle"],
      gravity: randomInSnow(0.4, 0.6),
      scalar: randomInSnow(0.4, 1),
      drift: randomInSnow(-0.4, 0.4),
    });

    if (timeLeft > 0) {
      requestAnimationFrame(frame);
    }
  })();

  function randomInRange(min, max) {
    return Math.random() * (max - min) + min;
  }

  var interval = setInterval(function () {
    var timeLeft = animationEnd - Date.now();

    if (timeLeft <= 0) {
      return clearInterval(interval);
    }

    var particleCount = 50 * (timeLeft / duration);
    // since particles fall down, start a bit higher than random
    confetti(
      Object.assign({}, defaults, {
        particleCount,
        origin: { x: randomInRange(0.1, 0.3), y: Math.random() - 0.2 },
      })
    );
    confetti(
      Object.assign({}, defaults, {
        particleCount,
        origin: { x: randomInRange(0.7, 0.9), y: Math.random() - 0.2 },
      })
    );
  }, 250);

  useEffect(() => {
    randomInRange();
    randomInSnow();
  }, []);
  return (
    <div className="content" style={{ background: "#121212" }}>
      <p className="content-text">
        NÃO IMPORTA A ANIMAÇÃO DE VOCÊS, ELA NÃO VAI SER O SUFICIENTE PARA
        DERROTAR O MELHOR GRUPO DE TODOS <span>BLACK POWER </span> VAI ENGOLIR A
        ANIMAÇÃO DE VOCÊS CUIDEM PARA NÃO AMARELAR, OU FICAREM VERDES DE ENJOO
      </p>
    </div>
  );
};

export default Modal;
